import React, { Component } from 'react';

import AppTable from '../app-table';
import AppHeader from '../app-header';
import AppFilter from '../app-filter';
import './app.css';

let id = 0;


const createMocks = (name, os, rom, ram, cpu, gpu, controller, rating) => {
  id += 1;

  return {
    id, name, os, rom, ram, cpu, gpu, controller, rating,
  };
};

export default class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      dataFromServer: [
        createMocks('Star Wars 1313', 'Windows', 2500, 2048, 3, 4, true, 8),
        createMocks('Minecraft', 'MacOS', 1000, 1000, 1, 1, true, 2),
        createMocks('Sims 3', 'Linux', 64000, 6000, 7, 4, false, 5),
        createMocks('Sex', 'MacOS', 3030, 3, 3, 4, false, 2),
        createMocks('Doka2', 'Linux', 1, 3, 3, 4, false, 6),
        createMocks('WoT', 'Windows', 10000, 10000, 10, 10, false, 10),
        createMocks('Worms', 'Linux', 10, 10, 1, 1, false, 3),
        createMocks('Silent Hill', 'MacOS', 2000, 3000, 3, 4, true, 10),
        createMocks('Detroit: Become Human', 'Linux', 100000, 10000, 10, 10, true, 10),
        createMocks('God Of War', 'Linux', 100000, 10000, 10, 10, true, 9),
        createMocks('APEX', 'Windows', 50000, 5000, 5, 5, false, 5),
      ],
      filter: {
        term: '',
        rating: 3,
        ram: 1000,
        rom: 1000,
        cpu: 1,
        gpu: 1,
        os: '',
        controller: false,
      },
    };
  }

  handleChange = (stateKey, value) => {
    this.setState(prevState => ({
      filter: {
        ...prevState.filter,
        [stateKey]: value,
      },
    }));
  };

  filterItems = (items, filter) => {
    const {
      term, ram, rom, cpu, gpu, rating, os, controller,
    } = filter;
    return this.search(items, term).filter(
      item => item.ram <= ram
        && item.rating >= rating
        && item.rom <= rom
        && item.cpu <= cpu
        && item.gpu <= gpu
        && (!os || item.os === os)
        && item.controller === controller,
    );
  };

  search = (items, term) => {
    if (term.length === 0) {
      return items;
    }
    return items.filter(item => (item.name.toLowerCase().indexOf(term.toLowerCase()) > -1));
  };

  render() {
    const { dataFromServer, filter } = this.state;
    const visibleItems = this.filterItems(dataFromServer, filter);
    return (
      <div className="games-app">
        <AppHeader />
        <AppFilter handleChange={this.handleChange} filterState={filter} />
        <AppTable data={visibleItems} />
      </div>
    );
  }
}
